.search-box{
    font-size: 50px;
    padding: 35px 35px;
    border-start-start-radius: 90px;
    border-bottom-left-radius: 90px;
}

.search-box:focus{
    outline: none;
    border: 1px solid black;
}


.search-btn{
    font-size: 45px;
    padding: 37px 50px 40px 35px;
    border-end-end-radius: 90px;
    border-top-right-radius: 90px;
    cursor: pointer;
    border: none;
    margin-left: 1px;
}